<template>
  <div class="pageContent">
    <!-- 筛选条件 -->
    <el-card class="filter-container" shadow="never">
      <div class="flex-row-between">
        <div>
          <div class="h3">买家上门自提功能</div>
          <div class="h3word">启用上门自提功能后,买家可以就近选择你预设的自提点,下单后你需要尽快将商品配送至指定自提点。</div>
        </div>
        <div>
          <el-switch
            v-model="selffedchStatus"
            active-color="#13ce66"
            active-value="1"
            inactive-value="0"
            inactive-color="#ff4949"
            @change="changeStatus">
          </el-switch>
        </div>
      </div>
    </el-card>
    <!-- 数据列表 - head-->
    <el-card class="operate-container" style="margin-top:10px" shadow="never">
      <div class="flex-row-between" style="width:100%;">
        <div>
          <i class="el-icon-tickets"></i>
          <span>数据列表</span>
        </div>
        <div style="padding-right:20px;">
          <el-button type="text" icon="el-icon-circle-plus-outline" @click="$router.push({path:'/logistics/selfPickUp/newSelf'})">新增自提点</el-button>
        </div>
      </div>
    </el-card>
    <!-- 数据列表 -->
    <el-card style="margin-top:20px;" class="filter-container" shadow="never">
      <el-table :data="list" border style="width: 100%">
        <el-table-column label="自提点名称" prop="name"></el-table-column>
        <el-table-column label="联系地址">
          <template slot-scope="scope">
            {{scope.row.provinceName + scope.row.cityName + scope.row.districtName + scope.row.address}}
          </template>
        </el-table-column>
        <el-table-column label="联系电话" prop="phoneNumber"></el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button type="text" size="small" @click="$router.push({path:'/logistics/selfPickUp/editSelf',query:{id:scope.row.id}})">编辑</el-button>
            <el-button @click="deleteItem(scope.row.id)" style="color:#F56C6C;" type="text" size="small">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!-- 分页 -->
    <div class="pageDown">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :page-sizes="[5, 10, 15, 20]"
        :page-size="searchList.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="listTotal">
      </el-pagination>
    </div>
    
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { 
  shopSelffetchPage,
  getShopSelffetch,
  setShopSelffetch,
  deleteShopSelffetch
} from '@/api/logistics/selfPickUp'
export default {
  data(){
    return{
      searchList:{
        pageNum:1,
        pageSize:5
      },              // 搜索栏数据
      list:[],                    // 列表数据
      listTotal:0,
      selffedchStatus:'0'
    }
  },
  created(){
    this.searchList.shopId = this.shop.shopId
    this.getData()
    this.getStatus()
  },
  methods:{
    // 数据列表
    getData(){
      shopSelffetchPage(this.searchList).then(res=>{
        console.log('数据列表',res)
        if(res.code == 200){
          this.list = res.data.list
          this.listTotal = res.data.total
        }
      })
    },
    // 获取是否开启了自提状态
    getStatus(){
      getShopSelffetch(this.shop.shopId).then(res=>{
        console.log('是否开启了自提',res)
        this.selffedchStatus = res.data
      })
    },
    // 更改自提状态
    changeStatus(){
      setShopSelffetch(this.shop.shopId,this.selffedchStatus).then(res=>{
        console.log('设置门店自提',res)
        if(res.code == 200){
          this.$message({
            message: res.message,
            type: 'success'
          });
        }else{
          this.$message.error(res.message);
        }
        this.getStatus()
      })
    },
    // 删除自提点
    deleteItem(id){
      this.$confirm('此操作将删除该数据', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        deleteShopSelffetch(id,this.shop.shopId).then(res=>{
          console.log('删除自提点',res)
          if(res.code == 200){
            this.$message({
              message: res.message,
              type: 'success'
            });
            this.getData()
          }else{
            this.$message.error(res.message);
          }
        })
      }).catch(() => {});
      
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.searchList.pageSize = val;
      this.getData()
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.searchList.pageNum = val;
      this.getData()
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>
<style lang="less" scoped>
  .h3{
    font-weight: bold;
    font-size: 16px;
  }
  .h3word{
    margin-top: 4px;
    font-size: 12px;
    color: #909399;
  }
  .pageDown{
    padding: 20px 0;
  }
</style>